html, body, .h-100 {
    height: 100% !important;
}

.flex-direction-row {
  flex-direction: row;
}

.flex-direction-column {
  flex-direction: column;
}

.align-items-center {
  align-items: center;
}

.align-items-end {
  align-items: flex-end;
}

.justify-center {
  justify-content: center!important;
}

.justify-space-between {
  justify-content: space-between!important;
}

.justify-end {
  justify-content: flex-end;
}

/* White and Black Palette*/
.primary-black-color {
  color: black !important;
}

.primary-black-background-color {
  background-color: black !important;
}

.primary-grey-color {
  color: #bdbdbd !important;
}

.primary-grey-background-color {
  background-color: #bdbdbd !important;
}

.secondary-dark-grey-color {
  color: #757575 !important;
}

.secondary-dark-grey-background-color {
  background-color: #757575 !important;
}

.secondary-light-grey-color {
  color: #eeeeee !important;
}

.secondary-light-grey-background-color {
  background-color: #fbfbfb !important;
}

.primary-white-color {
  color: white !important;
}

.primary-white-background-color {
  background-color: white !important;
}

/* Main color Palette*/
.primary-palette-color {
  color: #21252e !important;
}

.primary-palette-background-color {
  background-color: #21252e !important;
}

.secondary-dark-palette-color {
  color: #000000 !important;
}

.secondary-dark-palette-background-color {
  background-color: #000000 !important;
}

.secondary-light-palette-color {
  color: #494d57 !important;
}

.secondary-light-palette-background-color {
  background-color: #494d57 !important;
}

.primary-detail-color {
  color: #007bff !important;
}

.primary-background-detail-color {
  background-color: #007bff !important;
}

.secondary-dark-detail-color {
  color: #002153 !important;
}

.secondary-light-detail-color {
  color: #5c73b0 !important;
}

